// import valueOnLength from './util'
import { valueOnLength, calRecurring, dailyDeposit } from './util'
const daysLevels = [0.5, 1, 1.5, 2]

function calculator(
  amountInUSD = 500,
  length = 180,
  avgTransaction = 500,
  currentTime = [],
  calType = '',
  timePeriod = ''
) {
  const result = {}
  if (calType === 'holding') {
    const daysLevelsDict = daysLevels.map((d) => d * length)
    const chartData = []
    chartData.push({
      days: 0,
      value: amountInUSD,
      growth: 0,
      cost: amountInUSD
    })
    let value = 0
    daysLevelsDict.forEach((d) => {
      value = valueOnLength(
        amountInUSD,
        d,
        avgTransaction,
        currentTime,
        calType
      )
      chartData.push({
        timeLength: `${d} days`,
        value,
        growth: value - amountInUSD,
        cost: amountInUSD
      })
    })

    const lastTimeStampItem = chartData[chartData.length - 3] || 0
    result.totalValueAtTheEnd = lastTimeStampItem.value
    result.totalGrowth = lastTimeStampItem.growth
    result.chartData = chartData
    return result
  } else {
    const timeIndex = { daily: 1, weekly: 7, monthly: 30 }
    const daysLevelsDict = Array.from(Array(Number(length + 1)).keys())
    daysLevelsDict.shift()
    const timeDisc = { daily: 'days', weekly: 'weeks', monthly: 'months' }
    let newChartData = []
    if (length > 0 && timePeriod === 'daily') {
      newChartData = dailyDeposit(
        amountInUSD,
        length * timeIndex[timePeriod] - 1,
        avgTransaction,
        currentTime,
        timeIndex[timePeriod],
        timeDisc[timePeriod]
      )
    } else {
      newChartData = calRecurring(
        amountInUSD,
        length * timeIndex[timePeriod] - 1,
        avgTransaction,
        currentTime,
        timeIndex[timePeriod],
        timeDisc[timePeriod]
      )
    }
    const lastTimeStampItem = newChartData[newChartData.length - 1] || 0
    result.totalValueAtTheEnd = lastTimeStampItem.value
    result.totalGrowth = lastTimeStampItem.growth || 0
    result.chartData = newChartData
    return result
  }
}

export default calculator
