import React, { useState, useEffect } from 'react'
import { calculator } from '../../utils'

import {
  LineChart,
  Line,
  XAxis,
  YAxis,
  Tooltip,
  Legend,
  ResponsiveContainer
} from 'recharts'
import { withTranslation } from 'react-i18next'

const CALCULATOR_MODE = 'holding'
const SimpleCalculator = ({ avgDay, avgTransaction }) => {
  const DEFAULT_DAYS = 30 * 6
  const [amount, setAmount] = useState(10000)
  const [length, setLength] = useState(DEFAULT_DAYS)
  const [transactions, setTransactions] = useState(450)
  const [result, setResult] = useState(0)
  const [chartData, setChartData] = useState([])
  const [totalGrowth, setTotalGrowth] = useState('0')

  useEffect(() => {
    getResult()
  }, [amount, length, transactions])

  const getResult = () => {
    const currentDate = new Date()
    const result =
      calculator(
        amount,
        length,
        transactions,
        currentDate.valueOf(),
        CALCULATOR_MODE
      ) || 0
    const endValueInUSD = result.totalValueAtTheEnd
    const chartData = result.chartData
    const totalGrowth = result.totalGrowth
    console.log('result', result)
    setChartData(chartData)
    setResult(endValueInUSD)
    setTotalGrowth(totalGrowth)
  }

  return (
    <>
      <div className="info-session">
        <form>
          <div>
            <>
              If I hold
              <input
                type="number"
                value={amount}
                onChange={(e) => setAmount(e.target.value)}
              />
              <b>USD</b> for
              <input
                type="number"
                min={1}
                value={length}
                onChange={(e) => setLength(e.target.value)}
              />
              days,
              <br />
            </>
            and I assume
            <div className="ui input">
              <input
                type="number"
                value={transactions}
                onChange={(e) => setTransactions(e.target.value)}
              />
            </div>
            transactions take place every day. <br />
            <small className="notes">
              References: there are about <b>{avgTransaction}</b> transactions
              per day in the last <b>{avgDay}</b> days
            </small>
          </div>
        </form>
        <div className="result-text">
          The value is estimated to become{' '}
          <b>
            {result.toLocaleString('en-US', {
              style: 'currency',
              currency: 'USD'
            })}
          </b>{' '}
          USD at the end of the period.
        </div>
        <div className="result-text">
          The total potential growth is{' '}
          <b>
            {totalGrowth.toLocaleString('en-US', {
              style: 'currency',
              currency: 'USD'
            })}
          </b>{' '}
          USD at the end of the period.
        </div>
      </div>
      <div className="chart-container">
        <ResponsiveContainer>
          <LineChart
            data={chartData}
            margin={{
              top: 10,
              right: 20,
              left: 10,
              bottom: 10
            }}
          >
            <XAxis dataKey="timeLength" />
            <YAxis />
            <Tooltip />
            <Legend />
            <Line
              type="monotone"
              unit="USD"
              dataKey="value"
              strokeWidth="3px"
              stroke="#3a2bac"
              activeDot={{ r: 8 }}
            />
            <Line
              type="monotone"
              unit="USD"
              dataKey="growth"
              strokeWidth="3px"
              stroke="#00bfc7"
            />
          </LineChart>
        </ResponsiveContainer>
      </div>
    </>
  )
}

export default withTranslation()(SimpleCalculator)
