const DecayPeriod = 10957
const Start = 100 / 100
const End = 5 / 100
const DailyAPRDecay = Math.pow(1 + (End - Start), 1 / DecayPeriod)
const contractStartDay = new Date('10/24/2020')
const DPRStart = Math.pow(2, 1 / 365)
const DPREnd = Math.pow(1.05, 1 / DecayPeriod)
const DPRDecay = Math.pow(1 + (DPREnd - DPRStart), 1 / DecayPeriod)

const calDPR = (day) => {
  const preAPR = Math.pow(DailyAPRDecay, day - 2)
  const preEDCR = Math.pow(Start * preAPR + 1, 1 / 365) - 1
  return preEDCR * DPRDecay
}

export const valueOnLength = (
  initialValue,
  length,
  avgTransaction = 500,
  currentTime
) => {
  const daysDiff = Math.round(
    (currentTime - contractStartDay.valueOf()) / (1000 * 60 * 60 * 24)
  )
  let count = daysDiff
  while (count < length + daysDiff) {
    count++
    initialValue = initialValue * (1 + calDPR(count)) + avgTransaction / 10000
  }
  return initialValue
}

export const dailyDeposit = (
  depositValue,
  lengthInDays,
  avgTransaction = 500,
  currentTime,
  timePeriod,
  timeString
) => {
  const chartData = []
  const daysDiff = Math.round(
    (currentTime - contractStartDay.valueOf()) / (1000 * 60 * 60 * 24)
  )
  let count = daysDiff
  let totalValue = depositValue
  if (count === daysDiff) {
    chartData.push({
      timeLength: 0,
      value: depositValue,
      growth: 0
    })
  }
  while (count < lengthInDays + daysDiff + 1) {
    count++
    const countOfDeposit = Math.floor((count - daysDiff) / timePeriod) + 1
    totalValue =
      (totalValue + depositValue) * (1 + calDPR(count)) + avgTransaction / 10000
    chartData.push({
      timeLength: `${Math.floor(count - daysDiff) / timePeriod} ${timeString}`,
      value: totalValue,
      growth: totalValue - countOfDeposit * depositValue
    })
  }
  return chartData
}

export const calRecurring = (
  depositValue,
  lengthInDays,
  avgTransaction = 500,
  currentTime,
  timePeriod,
  timeString
) => {
  const chartData = []
  const daysDiff = Math.round(
    (currentTime - contractStartDay.valueOf()) / (1000 * 60 * 60 * 24)
  )
  let count = daysDiff
  let totalValue = depositValue
  if (count === daysDiff) {
    chartData.push({
      timeLength: 0,
      value: depositValue,
      growth: 0
    })
  }
  while (count < lengthInDays + daysDiff + 1) {
    count++
    //  how far away from the current, if it is a month/week/day away, then deposit
    if (
      (count - daysDiff) % timePeriod === 0 &&
      count === daysDiff + lengthInDays + 1
    ) {
      const countOfDeposit = Math.ceil((count - daysDiff) / timePeriod)
      totalValue = totalValue * (1 + calDPR(count)) + avgTransaction / 10000
      chartData.push({
        timeLength: `${count - daysDiff} days`,
        value: totalValue,
        growth: totalValue - countOfDeposit * depositValue
      })
    } else if (
      (count - daysDiff) % timePeriod === 0 &&
      count !== daysDiff + lengthInDays - 1
    ) {
      const countOfDeposit = Math.floor((count - daysDiff) / timePeriod) + 1
      totalValue =
        (totalValue + depositValue) * (1 + calDPR(count)) +
        avgTransaction / 10000
      chartData.push({
        timeLength: `${
          Math.floor(count - daysDiff) / timePeriod
        } ${timeString}`,
        value: totalValue,
        growth: totalValue - countOfDeposit * depositValue
      })
    } else {
      const countOfDeposit = Math.ceil((count - daysDiff) / timePeriod)
      totalValue = totalValue * (1 + calDPR(count)) + avgTransaction / 10000
      chartData.push({
        timeLength: `${count - daysDiff} days`,
        value: totalValue,
        growth: totalValue - countOfDeposit * depositValue
      })
    }
  }
  return chartData
}
